import React from 'react';
import './App.css';
import Item from './Item';
import { mainQuestions, supplementQuestions } from './QuestionList'
import Jumbotron from './Jumbotron';
import ProgressBar from './ProgressBar';
import { States, createWeights } from './States'
import Hello from './Questions/Hello'
import Goodbye from './Goodbye';

//results
let helloData = {}
let totalWeights = createWeights(States)
let answers = []

//section
let sectionsStatesObj = { showHello: true, showQuestions: false, showGoodbye: false }
//questions
let questionsList = mainQuestions
let sectionsNames = ['mainQuestions', 'supplementQuestions']
let activeSectionName = sectionsNames[0]
let questionPointer = 0

//Local Storage
let restoredFromLSDone = false


function saveAnswerToArr(answeredQuestion) {
  answers.push(answeredQuestion)

  if (answeredQuestion.answer.hasOwnProperty("weights"))
    answeredQuestion.answer.weights.forEach(element => {
      totalWeights[element.weight.id].value += element.value
    });

  console.log(totalWeights)
}

function saveToLocalStorage() {
  localStorage.setItem('questionPointer', questionPointer)
  localStorage.setItem('activeSectionName', activeSectionName)

  localStorage.setItem('sectionsStatesObj', JSON.stringify(sectionsStatesObj))

  localStorage.setItem('answers', JSON.stringify(answers))
  localStorage.setItem('helloData', JSON.stringify(helloData))
  localStorage.setItem('totalWeights', JSON.stringify(totalWeights))
}

function restoreFromLocalStorage() {
  if (localStorage.getItem('questionPointer')) {
    questionPointer = localStorage.getItem('questionPointer')
    activeSectionName = localStorage.getItem('activeSectionName')

    sectionsStatesObj = JSON.parse(localStorage.getItem('sectionsStatesObj'))

    answers = JSON.parse(localStorage.getItem('answers'))
    helloData = JSON.parse(localStorage.getItem('helloData'))
    totalWeights = JSON.parse(localStorage.getItem('totalWeights'))
  }
}

function buildSupplenetsQuestionsList(requiredStatesArr) {
  let result = []
  const targetStates = requiredStatesArr //todo
  supplementQuestions.forEach(question => {
    if (targetStates.includes(question.correspondingState)) {
      result.push(question)
    }
  })

  return result
}

function App() {
  if (!restoredFromLSDone) {
    restoreFromLocalStorage()
    restoredFromLSDone = true
  }
  const [activeQuestion, setActiveQuestion] = React.useState(questionsList[questionPointer])
  const [progressBarState, setProgressBarState] = React.useState(parseInt(((questionPointer / questionsList.length) * 100), 10))
  const [sectionsState, setSectionStates] = React.useState(sectionsStatesObj)

  console.log(buildSupplenetsQuestionsList([States[3]]))

  function sendHandler() {
    fetch('/save', {
      method: 'POST', // *GET, POST, PUT, DELETE, etc.
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ data: answers })
    })
  }

  function saveHelloData(data) {
    helloData = data
    //start questions
    setSectionStates({ ...sectionsState, showHello: false, showQuestions: true })
  }

  function nextQuestionHandler(answeredQuestion) {
    saveAnswerToArr(answeredQuestion)

    questionPointer++
    if (questionPointer < questionsList.length)
      setActiveQuestion(questionsList[questionPointer])

    if (activeSectionName === 'mainQuestions' && questionPointer === questionsList.length) {
      questionPointer = 0
      activeSectionName = 'supplementQuestions'
      questionsList = supplementQuestions
      setActiveQuestion(questionsList[questionPointer])
    }
    if (activeSectionName === 'supplementQuestions' && questionPointer === questionsList.length) {
      setSectionStates({ ...sectionsState, showHello: false, showQuestions: false, showGoodbye: true })
    }

    setProgressBarState(parseInt(((questionPointer / questionsList.length) * 100), 10))

    saveToLocalStorage()
    //console.log(sectionPointer)
    //console.log(answers)
  }

  return (
    <div className="App" >
      <Jumbotron />
      <div className="w-50 mx-auto">
        {sectionsState.showHello &&
          <Hello
            saveHelloData={saveHelloData}
            mainActiveAreas={sectionsState}
            setMainActiveAreas={setSectionStates}
          />}
        {sectionsState.showQuestions &&
          <div>
            <ProgressBar progressBarState={progressBarState} />
            <Item
              activeQuestion={activeQuestion}
              nextQuestionHandler={nextQuestionHandler} />
          </div>}
        {sectionsState.showGoodbye &&
          <div>
            <Goodbye />
          </div>
        }
      </div>
    </div>
  );
}

export default App;
